
import { computed, defineComponent, onMounted, ref, watch } from 'vue'
import {
    IonPage,
    IonContent,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonBackButton,
    IonTitle,
    IonGrid,
    IonRow,
    IonCol,
    IonText,
    IonChip,
    IonLabel,
    IonSearchbar,
    IonButton,
    IonIcon,
    IonList,
    IonItem,
    IonNote,
    loadingController,
    alertController
} from '@ionic/vue';
import NoDataVue from '@/components/NoData.vue';
import HomeButtonVue from '@/components/HomeButton.vue';
import characters from '@/jsons/characters.json';
import NeopleApi from '@/utils/NeopleApi';
import { Swiper, SwiperSlide } from 'swiper/vue';
import { Pagination } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';
import '@ionic/vue/css/ionic-swiper.css';
import { search, refresh } from 'ionicons/icons';
import Common from '@/utils/Common';

type RankingType = "winCount" | "winRate" | "killCount" | "assistCount" | "exp";

export default defineComponent({
    components: {
        IonPage,
        IonContent,
        IonHeader,
        IonToolbar,
        IonButtons,
        IonBackButton,
        IonTitle,
        IonGrid,
        IonRow,
        IonCol,
        IonText,
        IonChip,
        IonLabel,
        IonSearchbar,
        IonButton,
        IonIcon,
        IonList,
        IonItem,
        IonNote,
        NoDataVue,
        HomeButtonVue,
        Swiper,
        SwiperSlide
    },
    setup() {
        const rankingTypes = ref([
            {
                typeKor: '승리수',
                typeEng: 'winCount',
                color: ''
            },
            {
                typeKor: '승률',
                typeEng: 'winRate',
                color: 'medium'
            },
            {
                typeKor: '킬',
                typeEng: 'killCount',
                color: 'medium'
            },
            {
                typeKor: '도움',
                typeEng: 'assistCount',
                color: 'medium'
            },
            {
                typeKor: '경험치',
                typeEng: 'exp',
                color: 'medium'
            }
        ])
        const characterId = ref<string>(characters[0].characterId);
        const rankingType = ref<RankingType>(rankingTypes.value[0].typeEng as RankingType);
        const searchWord = ref<string>('');
        const list = ref<any[]>([]);
        const characterGrid = ref<any[]>([]);
        const offset = ref(0);

        let slides: any = null;
        let row: any[] = [];

        characters.forEach((data, i) => {
            row.push({
                ...data,
                class: i === 0 ? 'active' : '',
                color: i === 0 ? 'primary' : 'medium'
            });
            if ((i + 1) % 5 === 0) {
                characterGrid.value.push(row);
                row = [];
            }
        });

        const characterClick = (data: string) => {
            characterId.value = data;
        }

        const rankingTypeClick = (data: RankingType) => {
            rankingType.value = data;
        }

        const onSwiper = (swiper: any) => {
            slides = swiper;
        }

        const getRanking = async (refresh?: boolean) => {
            if (!Common.isNull(refresh) && refresh) {
                list.value = [];
                offset.value = 0;
                searchWord.value = '';
            }

            const loading = await loadingController.create({
                message: `캐릭터 '${characters.filter(data => data.characterId === characterId.value)[0].characterName}'<br>랭킹 조회중`,
                mode: 'ios'
            })

            await loading.present();

            try {
                const data = await NeopleApi.cyCharacterRanking({
                    characterId: characterId.value,
                    rankingType: rankingType.value,
                    offset: offset.value
                })

                if (Common.isNull(data.rows)) {
                    list.value = [];
                    offset.value = 0;
                } else {
                    list.value = [...list.value, ...data.rows];
                    offset.value += data.rows.length < 10 ? 0 : 10;
                }
            } catch (error) {
                const alert = await alertController.create({
                    header: '오류 발생',
                    subHeader: `${error}`,
                    buttons: ['ok'],
                    mode: 'ios'
                })

                await alert.present();
            } finally {
                await loading.dismiss();
                slides.slideTo(1);
            }
        }

        const showAddBtn = computed(() => offset.value > 0);

        watch(characterId, () => {
            characterGrid.value.map((row: any[]) => {
                row.map(data => {
                    if (data.characterId === characterId.value) {
                        data.class = 'active';
                        data.color = 'primary';
                    } else {
                        data.class = '';
                        data.color = 'medium';
                    }
                    return data;
                })
                return row;
            })

            getRanking(true);
        })

        watch(rankingType, () => {
            rankingTypes.value.map(data => {
                data.color = data.typeEng === rankingType.value ? '' : 'medium';
                return data;
            })

            getRanking(true);
        })

        watch(list, () => {
            list.value.map(data => {
                data.winRatio = Math.round((data.winCount / (data.winCount + data.loseCount) * 10000)) / 100;
                return data;
            })
        })

        onMounted(() => {
            getRanking(true);
        })

        return {
            NeopleApi,
            Common,
            characterGrid,
            rankingTypes,
            search,
            refresh,
            searchWord,
            list,
            modules: [Pagination],
            showAddBtn,
            rankingType,
            characterClick,
            rankingTypeClick,
            onSwiper,
            getRanking
        }
    },
})
