
import {
  IonPage,
  IonContent,
  IonCard,
  IonHeader,
  IonToolbar,
  IonSearchbar,
  IonButtons,
  IonButton,
  IonIcon,
  IonTitle,
  IonChip,
  IonLabel,
  IonGrid,
  IonRow,
  IonCol,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  IonMenuButton,
  IonMenu,
  IonList,
  IonItem,
  alertController,
  onIonViewDidLeave,
  onIonViewWillEnter,
  menuController,
} from "@ionic/vue";
import { defineComponent, onMounted, ref, watch } from "vue";
import { search, close } from "ionicons/icons";
import Common from "@/utils/Common";
import { useRouter } from "vue-router";
import { IDBPDatabase, openDB } from "idb";
import menuList from "@/jsons/menuList.json";

export default defineComponent({
  components: {
    IonPage,
    IonContent,
    IonCard,
    IonHeader,
    IonToolbar,
    IonSearchbar,
    IonButtons,
    IonButton,
    IonIcon,
    IonTitle,
    IonChip,
    IonLabel,
    IonGrid,
    IonRow,
    IonCol,
    IonCardHeader,
    IonCardTitle,
    IonCardContent,
    IonMenuButton,
    IonMenu,
    IonList,
    IonItem,
  },
  setup() {
    const router = useRouter();
    let idb: IDBPDatabase<unknown>;

    const searchWord = ref("");
    const searchWords = ref();
    const tableName = "search-words";

    const menuGrid: any[] = [];
    let row: any[] = [];

    menuList.forEach((data, i) => {
      row.push(data);
      if ((i + 1) % 2 === 0) {
        menuGrid.push(row);
        row = [];
      }
    });

    const movePage = async (word: string) => {
      if (Common.isNull(word)) {
        const alert = await alertController.create({
          message: "닉네임을 입력 후 다시 시도해주세요",
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } else {
        router.push({
          path: `/playerInfo/${encodeURI(word)}`,
        });
      }
    };

    const enter = () => {
      setTimeout(() => {
        movePage(searchWord.value);
      }, 250);
    };

    const searchBtnClick = () => {
      movePage(searchWord.value);
    };

    const searchWordsClick = async (word: string) => {
      movePage(word);
    };

    const searchWordsDelete = async (date: Date) => {
      await idb.delete(tableName, date);
      searchWords.value = await idb.getAll(tableName);
    };

    watch(searchWords, () => {
      (searchWords.value as any[]).sort((a, b) => b.date - a.date);
    });

    onIonViewWillEnter(async () => {
      if (!Common.isNull(idb?.name))
        searchWords.value = await idb.getAll(tableName);
    });

    onIonViewDidLeave(() => {
      searchWord.value = "";
      menuController.close("mainMenu");
    });

    onMounted(async () => {
      idb = await openDB("jcu", 1, {
        upgrade(db) {
          if (!db.objectStoreNames.contains(tableName)) {
            db.createObjectStore(tableName);
          }
        },
      });

      searchWords.value = await idb.getAll(tableName);
    });

    return {
      Common,
      search,
      close,
      searchWord,
      searchWords,
      menuGrid,
      enter,
      searchBtnClick,
      searchWordsClick,
      searchWordsDelete,
    };
  },
});
