    IonContent,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonButtons,
    IonBackButton,
    IonList,
    IonItem,
    IonLabel,
} from '@ionic/vue'
import OpenSourceLicense from '@/jsons/openSourceLicense.json'
import HomeButtonVue from '@/components/HomeButton.vue'

export default defineComponent({
    components: {
        IonPage,
        IonContent,
        IonHeader,
        IonToolbar,
        IonTitle,
        IonButtons,
        IonBackButton,
        IonList,
        IonItem,
        IonLabel,
        HomeButtonVue
    },
    setup() {
        const licenses = ref(OpenSourceLicense);

        return {
            licenses
        }
    },
})
