
import { computed, defineComponent, onMounted, ref } from "vue";
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonButtons,
  IonButton,
  IonContent,
  IonList,
  IonItem,
  loadingController,
  alertController,
  IonLabel,
  IonThumbnail,
  IonTitle,
  IonBackButton,
  IonText,
} from "@ionic/vue";
import NeopleApi from "@/utils/NeopleApi";
import { useRoute } from "vue-router";
import HomeButtonVue from "@/components/HomeButton.vue";

export default defineComponent({
  components: {
    IonPage,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonButton,
    IonContent,
    IonList,
    IonItem,
    IonLabel,
    IonThumbnail,
    IonTitle,
    IonBackButton,
    IonText,
    HomeButtonVue,
  },
  setup() {
    const route = useRoute();
    const itemId = route.params.itemId as string;
    const itemInfo = ref();
    const detail = ref(false);

    const explain = computed(() => {
      if (detail.value) {
        return itemInfo.value.explainDetail;
      } else {
        return itemInfo.value.explain;
      }
    });

    const btnName = computed(() =>
      detail.value ? "간략히보기" : "자세히보기"
    );

    const detailView = () => {
      detail.value = !detail.value;
    };

    const getItemColor = (
      rarityName: "커먼" | "언커먼" | "레어" | "유니크"
    ) => {
      let color = "";

      switch (rarityName) {
        case "유니크":
          color = "unique";
          break;

        case "레어":
          color = "rare";
          break;

        case "언커먼":
          color = "primary";
          break;

        default:
          color = "dark";
          break;
      }

      return color;
    };

    onMounted(async () => {
      const loading = await loadingController.create({
        message: "데이터 조회중",
        mode: "ios",
      });

      await loading.present();

      try {
        itemInfo.value = await NeopleApi.cyItemsInfo({ itemId: itemId });
        itemInfo.value.itemColor = getItemColor(itemInfo.value.rarityName);
      } catch (error) {
        const alert = await alertController.create({
          header: "오류 발생",
          subHeader: `${error}`,
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } finally {
        await loading.dismiss();
      }
    });

    return {
      itemInfo,
      NeopleApi,
      detailView,
      explain,
      btnName,
    };
  },
});
